
import Vue from 'vue';
import { mapState, mapMutations } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'day-form',
data() {
return {
taskId:this.$route.query.id
}
},
computed: {
...mapState({
formModel: state => state.common.day.formModel,
types: state => state.common.day.types,
prList: state => state.common.day.prList,
vm: state => state.common.day.vm
})
},
components: {
XHeader,
Calendar,
XButton
},
created(){
if(this.taskId){
this.getItem(this.taskId);
}else{
this.formModel.createDate = this.$formatDate({
type:'yyyy-mm-dd'
})
}
},
methods: {
...mapMutations({
getPrItem:'common/day/getPrItem',
getItem:'common/day/getItem'
}),
handleSave(){
if(this.taskId){
this.$store.commit({
type:'common/day/edit'
})
}else{
this.$store.commit({
type:'common/day/add'
})
}
},
handleBack(){
window.history.go(-1);
},
handleChangeType() {
this.$store.commit({
type: 'common/day/changeType'
})
}
}
}



